import { useState, useEffect } from 'react'
import Cards from 'react-credit-cards-2'
import * as Yup from 'yup'

import { useDispatch } from 'react-redux'
import { Formik, Form } from 'formik'

import {
  closePayingForm,
  showSuccessNotification,
} from '../../redux/slices/payingFormSlice'
import ButtonClose from './ButtonClose/ButtonClose'
import styles from './style.module.scss'
import 'react-credit-cards-2/dist/lib/styles.scss'
import { PayingFormProps } from '../../@types/props/PayingFormProps'

const SignupSchema = Yup.object().shape({
  number: Yup.string()
    .min(16, '–ü–æ–ª–µ –¥–æ–ª–∂–Ω–æ —Å–æ–¥–µ—Ä–∂–∞—Ç—å –º–∏–Ω–∏–º—É–º 16 —Ü–∏—Ñ—Ä')
    .required('–ü–æ–ª–µ –æ–±—è–∑–∞—Ç–µ–ª—å–Ω–æ –¥–ª—è –∑–∞–ø–æ–ª–Ω–µ–Ω–∏—è'),
  name: Yup.string().required('–ü–æ–ª–µ –æ–±—è–∑–∞—Ç–µ–ª—å–Ω–æ –¥–ª—è –∑–∞–ø–æ–ª–Ω–µ–Ω–∏—è'),
  expiry: Yup.string()
    .min(4, '–ü–æ–ª–µ –¥–æ–ª–∂–Ω–æ —Å–æ–¥–µ—Ä–∂–∞—Ç—å –º–∏–Ω–∏–º—É–º 4 —Ü–∏—Ñ—Ä—ã')
    .required('–ü–æ–ª–µ –æ–±—è–∑–∞—Ç–µ–ª—å–Ω–æ –¥–ª—è –∑–∞–ø–æ–ª–Ω–µ–Ω–∏—è'),
  cvc: Yup.string()
    .min(3, '–ü–æ–ª–µ –¥–æ–ª–∂–Ω–æ —Å–æ–¥–µ—Ä–∂–∞—Ç—å –º–∏–Ω–∏–º—É–º 3 —Ü–∏—Ñ—Ä—ã')
    .required('–ü–æ–ª–µ –æ–±—è–∑–∞—Ç–µ–ª—å–Ω–æ –¥–ª—è –∑–∞–ø–æ–ª–Ω–µ–Ω–∏—è'),
})

const PayingForm = ({ onClick }: PayingFormProps) => {
  const [state, setState] = useState({
    number: '',
    expiry: '',
    cvc: '',
    name: '',
    focus: '',
  })

  const dispatch = useDispatch()

  const [successNotIfication, setSuccessNotification] = useState(false)

  const handleInputChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    let { name, value } = e.target

    value = value
      .trimStart()
      .toLocaleUpperCase()
      .replace(/[^a-z]/gi, '')
    setState((prev) => ({ ...prev, [name]: value }))
  }

  useEffect(() => {
    setTimeout(() => {
      setSuccessNotification(false)
    }, 2000)
  }, [successNotIfication])

  const handleInputCardNumber = (e: React.ChangeEvent<HTMLInputElement>) => {
    let { name, value } = e.target

    value = value.slice(0, 16)

    setState((prev) => ({ ...prev, [name]: value }))
  }

  const handleInputChangeCVC = (e: React.ChangeEvent<HTMLInputElement>) => {
    let { name, value } = e.target
    value = value.slice(0, 3)
    setState((prev) => ({ ...prev, [name]: value }))
  }

  const handleInputChangeExpiry = (e: React.ChangeEvent<HTMLInputElement>) => {
    let { name, value } = e.target
    value = value.slice(0, 4)
    setState((prev) => ({ ...prev, [name]: value }))
  }

  const handleInputFocus = (e: React.ChangeEvent<HTMLInputElement>) => {
    setState((prev) => ({ ...prev, focus: e.target.name }))
  }

  const handleSubmit = () => {
    dispatch(closePayingForm(false))
    dispatch(showSuccessNotification(true))
  }

  return (
    <div className={styles.card__block}>
      <ButtonClose classType="miniCloseLine" onClick={onClick} />
      <div className={styles.card__block__form}>
        <div className={styles.card__block__inner}>
          <Cards
            number={state.number}
            expiry={state.expiry}
            cvc={state.cvc}
            name={state.name}
            focused={state.focus}
          />
        </div>
        <Formik
          initialValues={{
            number: '',
            expiry: '',
            cvc: '',
            name: '',
          }}
          validationSchema={SignupSchema}
          onSubmit={handleSubmit}
        >
          {({ handleChange, errors, touched }) => (
            <Form className={styles.form}>
              <input
                type="number"
                name="number"
                placeholder="–ù–æ–º–µ—Ä –∫–∞—Ä—Ç—ã"
                value={state.number}
                onChange={(e) => [handleInputCardNumber(e), handleChange(e)]}
                onFocus={handleInputFocus}
              />
              <p className={styles.error}>
                {errors.number && touched.number && errors.number}
              </p>
              <input
                type="text"
                name="name"
                placeholder="–ò–º—è –≤–ª–∞–¥–µ–ª—å—Ü–∞ –∫–∞—Ä—Ç—ã"
                value={state.name}
                onChange={(e) => [handleInputChange(e), handleChange(e)]}
                onFocus={handleInputFocus}
              />
              <p className={styles.error}>
                {errors.name && touched.name && errors.name}
              </p>
              <input
                type="number"
                name="expiry"
                placeholder="–î–µ–π—Å—Ç–≤–∏—Ç–µ–ª—å–Ω–∞ –¥–æ"
                pattern="\d\d/\d\d"
                value={state.expiry}
                onChange={(e) => [handleInputChangeExpiry(e), handleChange(e)]}
                onFocus={handleInputFocus}
              />
              <p className={styles.error}>
                {errors.expiry && touched.expiry && errors.expiry}
              </p>
              <input
                type="number"
                name="cvc"
                placeholder="CVC"
                value={state.cvc}
                onChange={(e) => [handleInputChangeCVC(e), handleChange(e)]}
                onFocus={handleInputFocus}
              />
              <p className={styles.error}>
                {errors.cvc && touched.cvc && errors.cvc}
              </p>
              <div className={styles.formActions}>
                <button type="submit">–û–ø–ª–∞—Ç–∏—Ç—å</button>
              </div>
            </Form>
          )}
        </Formik>
      </div>
    </div>
  )
}

export default PayingForm
